"use client"

import { useEffect, useRef, useState } from "react"
import { ArrowUpRight, ExternalLink } from "lucide-react"

const projects = [
  {
    title: "TechFlow App",
    category: "Sistema Web",
    description: "Plataforma de gestão de projetos com dashboard interativo e integrações.",
    image: "/modern-dashboard-dark-theme-with-charts.jpg",
    color: "from-blue-500/20",
  },
  {
    title: "Bloom Cosmetics",
    category: "E-commerce",
    description: "Loja virtual de cosméticos com experiência de compra imersiva.",
    image: "/cosmetics-ecommerce-elegant-dark.jpg",
    color: "from-pink-500/20",
  },
  {
    title: "Nexus Finance",
    category: "Landing Page",
    description: "Landing page de alta conversão para fintech com animações impactantes.",
    image: "/fintech-landing-page-dark-modern.jpg",
    color: "from-green-500/20",
  },
  {
    title: "Urban Studio",
    category: "Site Institucional",
    description: "Website para estúdio de arquitetura com galeria de projetos.",
    image: "/architecture-portfolio-dark-minimal.jpg",
    color: "from-amber-500/20",
  },
  {
    title: "FitPro Academy",
    category: "Sistema Web",
    description: "Plataforma de aulas fitness com streaming e gestão de alunos.",
    image: "/fitness-app-dark-theme-workout.jpg",
    color: "from-red-500/20",
  },
  {
    title: "Legal Partners",
    category: "Site Institucional",
    description: "Website corporativo para escritório de advocacia com formulário de casos.",
    image: "/law-firm-website-dark-professional.jpg",
    color: "from-indigo-500/20",
  },
]

export default function PortfolioSection() {
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null)
  const [visibleCards, setVisibleCards] = useState<number[]>([])
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const index = Number.parseInt(entry.target.getAttribute("data-index") || "0")
            setVisibleCards((prev) => [...new Set([...prev, index])])
          }
        })
      },
      { threshold: 0.1 },
    )

    const cards = sectionRef.current?.querySelectorAll("[data-index]")
    cards?.forEach((card) => observer.observe(card))

    return () => observer.disconnect()
  }, [])

  return (
    <section id="portfolio" ref={sectionRef} className="py-24 md:py-32 relative">
      <div className="max-w-7xl mx-auto px-6">
        {/* Section Header */}
        <div className="flex flex-col md:flex-row md:items-end md:justify-between mb-16 gap-6">
          <div>
            <span className="inline-block text-primary text-sm font-medium tracking-wider uppercase mb-4">
              Portfólio
            </span>
            <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold text-foreground text-balance">
              Trabalhos
              <br />
              <span className="text-primary">selecionados</span>
            </h2>
          </div>
          <p className="text-lg text-muted-foreground max-w-md leading-relaxed">
            Conheça alguns dos projetos que desenvolvemos para clientes de diversos segmentos.
          </p>
        </div>

        {/* Projects Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {projects.map((project, index) => {
            const isVisible = visibleCards.includes(index)
            const isHovered = hoveredIndex === index

            return (
              <div
                key={index}
                data-index={index}
                onMouseEnter={() => setHoveredIndex(index)}
                onMouseLeave={() => setHoveredIndex(null)}
                className={`group relative bg-card border border-border rounded-2xl overflow-hidden cursor-pointer transition-all duration-700 ${
                  isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"
                }`}
                style={{ transitionDelay: `${index * 100}ms` }}
              >
                {/* Image */}
                <div className="relative aspect-[4/3] overflow-hidden">
                  <img
                    src={project.image || "/placeholder.svg"}
                    alt={project.title}
                    className={`w-full h-full object-cover transition-transform duration-700 ${
                      isHovered ? "scale-110" : "scale-100"
                    }`}
                  />
                  <div className={`absolute inset-0 bg-gradient-to-t ${project.color} to-transparent opacity-60`} />

                  {/* Overlay on Hover */}
                  <div
                    className={`absolute inset-0 bg-background/80 backdrop-blur-sm flex items-center justify-center transition-opacity duration-300 ${
                      isHovered ? "opacity-100" : "opacity-0"
                    }`}
                  >
                    <div className="flex items-center gap-2 text-primary">
                      <span className="font-medium">Ver Projeto</span>
                      <ExternalLink className="w-5 h-5" />
                    </div>
                  </div>
                </div>

                {/* Content */}
                <div className="p-6">
                  <div className="flex items-center justify-between mb-2">
                    <span className="text-xs text-primary font-medium uppercase tracking-wider">
                      {project.category}
                    </span>
                    <ArrowUpRight
                      className={`w-5 h-5 text-muted-foreground transition-all duration-300 ${
                        isHovered ? "text-primary translate-x-1 -translate-y-1" : ""
                      }`}
                    />
                  </div>
                  <h3 className="text-xl font-bold text-foreground mb-2 group-hover:text-primary transition-colors duration-300">
                    {project.title}
                  </h3>
                  <p className="text-sm text-muted-foreground leading-relaxed">{project.description}</p>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </section>
  )
}
