"use client"

import { useEffect, useRef, useState } from "react"
import { CheckCircle2, Users, Target, Lightbulb, Rocket } from "lucide-react"

const values = [
  {
    icon: Lightbulb,
    title: "Inovação",
    description: "Buscamos sempre as melhores e mais modernas soluções.",
  },
  {
    icon: Target,
    title: "Foco em Resultados",
    description: "Cada projeto é pensado para gerar impacto real no seu negócio.",
  },
  {
    icon: Users,
    title: "Parceria",
    description: "Trabalhamos lado a lado com nossos clientes em cada etapa.",
  },
  {
    icon: Rocket,
    title: "Entrega",
    description: "Compromisso com prazos e qualidade em todos os projetos.",
  },
]

const highlights = [
  "Equipe multidisciplinar de especialistas",
  "Metodologia ágil de desenvolvimento",
  "Suporte contínuo pós-lançamento",
  "Código limpo e documentado",
  "Performance e SEO otimizados",
  "Design responsivo para todos os dispositivos",
]

export default function AboutSection() {
  const [isVisible, setIsVisible] = useState(false)
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.2 },
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => observer.disconnect()
  }, [])

  return (
    <section id="sobre" ref={sectionRef} className="py-24 md:py-32 relative overflow-hidden">
      {/* Background */}
      <div className="absolute inset-0 bg-gradient-to-b from-background via-secondary/20 to-background" />
      <div className="absolute top-1/2 left-0 w-1/2 h-1/2 bg-primary/5 rounded-full blur-3xl -translate-y-1/2" />

      <div className="relative z-10 max-w-7xl mx-auto px-6">
        {/* Main Content */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-16 items-center mb-24">
          {/* Text Content */}
          <div
            className={`transition-all duration-1000 ${
              isVisible ? "opacity-100 translate-x-0" : "opacity-0 -translate-x-10"
            }`}
          >
            <span className="inline-block text-primary text-sm font-medium tracking-wider uppercase mb-4">
              Sobre Nós
            </span>
            <h2 className="text-4xl md:text-5xl font-bold text-foreground mb-6 text-balance">
              Por que escolher a <span className="text-primary">Code404</span>?
            </h2>
            <p className="text-lg text-muted-foreground leading-relaxed mb-8">
              Somos uma agência de desenvolvimento web apaixonada por transformar ideias em experiências digitais
              memoráveis. Com mais de 5 anos no mercado, combinamos criatividade, tecnologia e estratégia para entregar
              resultados que superam expectativas.
            </p>
            <p className="text-lg text-muted-foreground leading-relaxed mb-8">
              Nosso nome, <span className="text-primary font-semibold">Code404</span>, representa nossa filosofia:
              transformamos o que seria um erro em uma oportunidade de criar algo extraordinário.
            </p>

            {/* Highlights */}
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              {highlights.map((item, index) => (
                <div
                  key={index}
                  className="flex items-center gap-3 transition-all duration-500"
                  style={{ transitionDelay: `${index * 100}ms` }}
                >
                  <CheckCircle2 className="w-5 h-5 text-primary flex-shrink-0" />
                  <span className="text-muted-foreground">{item}</span>
                </div>
              ))}
            </div>
          </div>

          {/* Visual Element */}
          <div
            className={`relative transition-all duration-1000 delay-300 ${
              isVisible ? "opacity-100 translate-x-0" : "opacity-0 translate-x-10"
            }`}
          >
            <div className="relative aspect-square max-w-md mx-auto">
              {/* Main Image/Visual */}
              <div className="absolute inset-4 bg-gradient-to-br from-primary/20 to-primary/5 rounded-3xl border border-primary/20" />
              <div className="absolute inset-0 flex items-center justify-center">
                <div className="text-center">
                  <div className="text-8xl md:text-9xl font-bold text-primary/20">404</div>
                  <div className="text-2xl font-bold text-foreground -mt-4">
                    Error? No, <span className="text-primary">Experience!</span>
                  </div>
                </div>
              </div>

              {/* Floating Stats */}
              <div className="absolute -top-4 -right-4 bg-card border border-border rounded-2xl p-4 shadow-xl">
                <div className="text-3xl font-bold text-primary">50+</div>
                <div className="text-sm text-muted-foreground">Projetos</div>
              </div>
              <div className="absolute -bottom-4 -left-4 bg-card border border-border rounded-2xl p-4 shadow-xl">
                <div className="text-3xl font-bold text-primary">5+</div>
                <div className="text-sm text-muted-foreground">Anos</div>
              </div>
            </div>
          </div>
        </div>

        {/* Values */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
          {values.map((value, index) => {
            const Icon = value.icon
            return (
              <div
                key={index}
                className={`group text-center p-6 bg-card border border-border rounded-2xl hover:border-primary/50 transition-all duration-500 ${
                  isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"
                }`}
                style={{ transitionDelay: `${400 + index * 100}ms` }}
              >
                <div className="w-14 h-14 bg-primary/10 rounded-xl flex items-center justify-center mx-auto mb-4 group-hover:bg-primary/20 group-hover:scale-110 transition-all duration-300">
                  <Icon className="w-7 h-7 text-primary" />
                </div>
                <h3 className="text-lg font-bold text-foreground mb-2">{value.title}</h3>
                <p className="text-sm text-muted-foreground">{value.description}</p>
              </div>
            )
          })}
        </div>
      </div>
    </section>
  )
}
